import request from '../utils/request'

export default {
 createPayOrder(openId,orderId,amount,description) {
   return new Promise((resolve, reject) => {
     uni.showLoading({
       title: '...',
       mask: true
     });
     
     uni.request({
       url: "https://shenzhoupost.cn/shenzhoupost" + '/WxPay/createOrder',
       method: 'POST',
       header: {
         'Content-Type': 'application/x-www-form-urlencoded',
         'Authorization': uni.getStorageSync('token')
       },
       data: {
         orderId:orderId,
         openId,
         amount,
         description
       },
       success: (res) => {
         uni.hideLoading();
         console.log(':', res);
         resolve(res.data);
       },
       fail: (err) => {
         uni.hideLoading();
         console.error(':', err);
         reject(err);
       }
     });
   });
 },
 
 // 
 callWxPay(payParams, orderId) {
   return new Promise((resolve, reject) => {
     uni.showLoading({
       title: '...',
       mask: true
     });
     
     uni.requestPayment({
       ...payParams,
       success: (res) => {
         uni.hideLoading();
         console.log('', res);
         
         // 
         this.queryOrderStatus(orderId)
           .then(result => {
             if (result.success) {
               resolve({
                 success: true,
                 message: ''
               });
             } else {
               // 
               resolve({
                 success: false,
                 message: result.message || ''
               });
             }
           })
           .catch(err => {
             // 
             resolve({
               success: true,
               message: ''
             });
           });
       },
       fail: (err) => {
         uni.hideLoading();
         console.log('', err);
         
         if (err.errMsg === 'requestPayment:fail cancel') {
           reject({
             success: false,
             message: '',
             canceled: true
           });
         } else {
           reject({
             success: false,
             message: err.errMsg || '',
             canceled: false
           });
         }
       }
     });
   });
 },
 
 // 
 queryOrderStatus(orderId) {
   return new Promise((resolve, reject) => {
     uni.showLoading({
       title: '...',
       mask: true
     });
     
     uni.request({
       url: "https://shenzhoupost.cn/shenzhoupost" + `/WxPay/queryOrder`,
       method: 'GET',
       header: {
         'Authorization': uni.getStorageSync('token')
       },
       data: {
         orderId: orderId
       },
       success: (res) => {
         uni.hideLoading();
         console.log(':', res.data);
         if (res.data.code === 200 && res.data.data) {
           const state = res.data.data.trade_state;
           if (state === 'SUCCESS') {
             resolve({
               success: true,
               message: '',
               state: state
             });
           } else if (state === 'NOTPAY' || state === 'USERPAYING') {
             resolve({
               success: false,
               message: '',
               state: state
             });
           } else {
             resolve({
               success: false,
               message: res.data.data.trade_state_desc || '',
               state: state
             });
           }
         } else {
           reject({
             success: false,
             message: ''
           });
         }
       },
       fail: (err) => {
         uni.hideLoading();
         console.error('', err);
         reject({
           success: false,
           message: ''
         });
       }
     });
   });
 },
 
 // (++)
 async processPayment(openId, orderId, amount, description) {
   try {
     // 1. 
     const payOrderRes = await this.createPayOrder(openId, orderId, amount, description);
     if (payOrderRes.code !== 200 || !payOrderRes.data) {
       return {
         success: false,
         message: payOrderRes.message || ''
       };
     }
     
     // 2. 
     const payResult = await this.callWxPay(payOrderRes.data, orderId);
     return payResult;
   } catch (error) {
     console.error(':', error);
     return {
       success: false,
       message: error.message || '',
       canceled: error.canceled || false
     };
   }
 },
 
 // 
 applyRefund(orderId, refundAmount, reason) {
   return new Promise((resolve, reject) => {
     uni.showLoading({
       title: '...',
       mask: true
     });
     
     uni.request({
       url: "https://shenzhoupost.cn/shenzhoupost" + '/WxPay/refund',
       method: 'POST',
       header: {
         'Content-Type': 'application/x-www-form-urlencoded',
         'Authorization': uni.getStorageSync('token')
       },
       data: {
         orderId: orderId,
         refundAmount: refundAmount,
         reason: reason
       },
       success: (res) => {
         uni.hideLoading();
         console.log(':', res.data);
         if (res.data.code === 200) {
           resolve({
             success: true,
             message: '',
             data: res.data.data
           });
         } else {
           resolve({
             success: false,
             message: res.data.message || '',
             data: res.data.data
           });
         }
       },
       fail: (err) => {
         uni.hideLoading();
         console.error(':', err);
         reject({
           success: false,
           message: ''
         });
       }
     });
   });
 },
 
 // 
 queryRefundStatus(orderId, refundId) {
   return new Promise((resolve, reject) => {
     uni.showLoading({
       title: '...',
       mask: true
     });
     
     const params = { orderId };
     if (refundId) {
       params.refundId = refundId;
     }
     
     uni.request({
       url: "https://shenzhoupost.cn/shenzhoupost" + '/WxPay/queryRefund',
       method: 'GET',
       header: {
         'Authorization': uni.getStorageSync('token')
       },
       data: params,
       success: (res) => {
         uni.hideLoading();
         console.log(':', res.data);
         if (res.data.code === 200 && res.data.data) {
           const refundStatus = res.data.data.status;
           if (refundStatus === 'SUCCESS') {
             resolve({
               success: true,
               message: '',
               status: refundStatus,
               data: res.data.data
             });
           } else if (refundStatus === 'PROCESSING') {
             resolve({
               success: false,
               message: '',
               status: refundStatus,
               data: res.data.data
             });
           } else if (refundStatus === 'ABNORMAL') {
             resolve({
               success: false,
               message: '',
               status: refundStatus,
               data: res.data.data
             });
           } else {
             resolve({
               success: false,
               message: res.data.data.status_desc || '',
               status: refundStatus,
               data: res.data.data
             });
           }
         } else {
           reject({
             success: false,
             message: ''
           });
         }
       },
       fail: (err) => {
         uni.hideLoading();
         console.error(':', err);
         reject({
           success: false,
           message: ''
         });
       }
     });
   });
 }
} 