// API
import request from '@/utils/request' // 

export default {
  /**
   * 
   * @param {Object} data 
   * @returns {Promise} 
   */
  createOrder(data) {
    return request({
      url: '/PaymentFlow',
      method: 'POST',
      data
    })
  },
  
  /**
   * 
   * @param {String} orderId ID
   * @returns {Promise} 
   */
  queryOrderStatus(orderId) {
    return request({
      url: `/PaymentFlow/${orderId}`,
      method: 'GET'
    })
  },
  
  batchesAdd(data) {
    return request({
      url: '/PaymentFlow/batchesAdd',
      method: 'post',
      data
    })
  },
  
  /**
   * 
   * @param {Object} data 
   * @returns {Promise} 
   */
  createRefund(data) {
    return request({
      url: '/PaymentFlow/refund',
      method: 'POST',
      data
    })
  },
}