import request from '../utils/request'

const uploadApi = {
  /**
   * 
   * @param {Object} file - 
   * @param {string} name - 
   * @returns {Promise<string>} URL
   */
  uploadFile({ file, name }) {
    return new Promise((resolve, reject) => {
      uni.uploadFile({
        url: `https://shenzhoupost.cn/shenzhoupost/File/orderFileUpload`, // URL
        filePath: file,
        name: 'file', // 
        formData: {
          name: name // 
        },
        success: (uploadRes) => {
          // 
          let responseData = uploadRes.data
          try {
            responseData = JSON.parse(responseData)
          } catch (e) {
            console.log('JSON', e)
          }
          resolve(responseData)
        },
        fail: (err) => {
          reject(err)
        }
      })
    })
  }
}

export default uploadApi
