import request from '@/utils/request'

// 
export default {
  /**
   * 
   * @param {Object} data - 
   */
  createStatement(data) {
    return request({
      url: '/MonthlyStatement',
      method: 'POST',
      data
    })
  },
  
  /**
   * 
   * @param {String} id - ID
   */
  getStatementById(id) {
    return request({
      url: `/MonthlyStatement?id=${id}`,
      method: 'GET'
    })
  },
  
  /**
   * 
   * @param {Number} pageNum - 
   * @param {Number} pageSize - 
   * @param {String} monthlySettlementEligibilityId - ID
   * @param {String} userId - ID
   * @param {String} startTime - 
   * @param {String} endTime - 
   * @param {Number} type - 
   */
  pageStatement(pageNum, pageSize, monthlySettlementEligibilityId, userId, startTime, endTime, type) {
    let url = `/MonthlyStatement/page?pageNum=${pageNum}&pageSize=${pageSize}`;
    
    if (monthlySettlementEligibilityId) {
      url += `&monthlySettlementEligibilityId=${monthlySettlementEligibilityId}`;
    }
    
    if (userId) {
      url += `&userId=${userId}`;
    }
    
    if (startTime) {
      url += `&startTime=${startTime}`;
    }
    
    if (endTime) {
      url += `&endTime=${endTime}`;
    }
    
    if (type !== undefined && type !== null) {
      url += `&type=${type}`;
    }
    
    return request({
      url,
      method: 'GET'
    })
  },

  getAllPrice(id,startTime, endTime,userId) {
    let url = `/MonthlyStatement/getAllPrice?id=${id}`;
    
    if (userId) {
      url += `&userId=${userId}`;
    }
    
    if (startTime) {
      url += `&startTime=${startTime}`;
    }
    
    if (endTime) {
      url += `&endTime=${endTime}`;
    }
    
    return request({
      url,
      method: 'GET'
    })
  },
  
  payBills(id,startTime, endTime) {
    let url = `/MonthlyStatement/payBills?id=${id}`;
    
    if (startTime) {
      url += `&startTime=${startTime}`;
    }
    
    if (endTime) {
      url += `&endTime=${endTime}`;
    }
    
    return request({
      url,
      method: 'GET'
    })
  },
  
  /**
   * 
   * @param {String} monthlySettlementEligibilityId - ID
   * @param {String} userId - ID
   */
  getStatementList(monthlySettlementEligibilityId, userId) {
    let url = '/MonthlyStatement/list?';
    
    if (monthlySettlementEligibilityId) {
      url += `monthlySettlementEligibilityId=${monthlySettlementEligibilityId}&`;
    }
    
    if (userId) {
      url += `userId=${userId}`;
    }
    
    //  &  ?
    if (url.endsWith('&') || url.endsWith('?')) {
      url = url.slice(0, -1);
    }
    
    return request({
      url,
      method: 'GET'
    })
  },
  
  /**
   * 
   * @param {Object} data - 
   * @param {String} data.monthlySettlementEligibilityId - ID
   * @param {Number} data.amount - 
   * @param {Array} data.statementIds - ID
   */
  createMonthlyPayment(data) {
    return request({
      url: '/MonthlyStatement/payment',
      method: 'POST',
      data
    })
  }
} 