import { baseUrl } from '@/config/env.js'

// 
const requestConfig = {
  // URL
  baseUrl: baseUrl,
  // 
  header: {
    'Content-Type': 'application/json;charset=UTF-8'
  },
  // 
  timeout: 500000,
  // 
  dataType: 'json'
}

// 
const requestInterceptor = (config) => {
  // token
  const token = uni.getStorageSync('token')

  if (token) {
    config.header['Authorization'] = token
  }
  // console.log(config.url.split('/'))
  // if (config.url.split('/')[4]) {
  //   let u1 = config.url.split('/')[4]
  //   if (u1 == 'Message') {
  //     let u2 = config.url.split('/')[5].split('?')[0]
  //     if (!u2 == 'getNewMessage') {
  //       uni.showLoading({
  //         title: '...',
  //         mask: true
  //       })
  //     }
  //   } else {
  //     uni.showLoading({
  //       title: '...',
  //       mask: true
  //     })
  //   }
  // }
  // loading

  return config
}

// 
const responseInterceptor = (response) => {
  // loading
  uni.hideLoading()

  // 
  const { statusCode, data } = response

  // HTTP
  if (statusCode === 200) {
    // 
    if (data.code === 200 || data.code === 0) {
      // 
      return data.data
    } else if (data.code === 500) {
      console.log(data)
      // 
      uni.showToast({
        title: data.message || '',
        icon: 'none',
        duration: 2000
      })
      return Promise.reject(data)
    } else {
      // 
      handleError(data)
      return Promise.reject(data)
    }
  } else {
    // HTTP
    handleError(response)
    return Promise.reject(response)
  }
}

// 
const handleError = (error) => {
  // loading
  uni.hideLoading()
  console.log(error)
  let message = ''

  // HTTP
  switch (error.statusCode) {
    case 400:
      message = ''
      break
    case 401:
      message = ''
      // 
      uni.navigateTo({
        url: '/pages/login/login'
      })
      break
    case 403:
      message = ''
      break
    case 404:
      message = ''
      break
    case 408:
      message = ''
      break
    case 500:
      message = ''
      break
    case 501:
      message = ''
      break
    case 502:
      message = ''
      break
    case 503:
      message = ''
      break
    case 504:
      message = ''
      break
    case 505:
      message = 'HTTP'
      break
    default:
      message = ''
  }

  uni.showToast({
    title: message,
    icon: 'none',
    duration: 2000
  })
}

//  form-data 
const objectToFormData = (obj) => {
  return Object.keys(obj)
    .map(key => `${encodeURIComponent(key)}=${encodeURIComponent(obj[key])}`)
    .join('&')
}

// 
const request = (options = {}) => {
  // URL
  const isFullUrl = options.url.startsWith('http://') || options.url.startsWith('https://')
  
  //  form-data 
  if (options.isFormData && options.data) {
    options.data = objectToFormData(options.data)
  }
  
  // 
  const config = {
    ...requestConfig,
    ...options,
    // URLbaseUrl
    url: isFullUrl ? options.url : requestConfig.baseUrl + options.url
  }

  // 
  const newConfig = requestInterceptor(config)

  // responseTypeblob
  if (config.responseType === 'blob') {
    return new Promise((resolve, reject) => {
      uni.request({
        url: config.url,
        method: config.method || 'get',
        data: config.data,
        params: config.params,
        header: newConfig.header,
        responseType: 'arraybuffer', // uni-apparraybuffer
        success: (response) => {
          if (response.statusCode === 200) {
            // 







            

            // BlobArrayBufferFileSystem API
            resolve({
              data: response.data,
              type: response.header['Content-Type'] || 'image/png',
              _isArrayBuffer: true
            });

            


























          } else {
            reject(response);
          }
        },
        fail: (error) => {
          reject(error);
        }
      });
    });
  }

  // 
  return new Promise((resolve, reject) => {
    uni.request({
      ...newConfig,
      success: (response) => {
        resolve(responseInterceptor(response))
      },
      fail: (error) => {
        handleError(error)
        reject(error)
      }
    })
  })
}

//  request 
export default request
