import request from '../utils/request'

// 
export default {
  // 
  getNoticeList() {
    return request({
      url: '/Notice/list',
      method: 'get'
    })
  },

  // 
  getLatestNotices() {
    return request({
      url: '/Notice/latest',
      method: 'get'
    })
  },

  // 
  pageNotice(pageNum = 1, pageSize = 10, type) {
    let url = `/Notice/page?pageNum=${pageNum}&pageSize=${pageSize}`
    if (type !== undefined) {
      url += `&type=${type}`
    }
    return request({
      url,
      method: 'get'
    })
  },

  // 
  addNotice(notice) {
    return request({
      url: '/Notice',
      method: 'post',
      data: notice
    })
  },

  // 
  updateNotice(notice) {
    return request({
      url: '/Notice',
      method: 'put',
      data: notice
    })
  },

  // 
  deleteNotice(noticeId) {
    return request({
      url: `/Notice/${noticeId}`,
      method: 'delete'
    })
  }
} 