import request from '@/utils/request'

const monthlyApi = {
	/**
	 * 
	 * @param {Object} params
	 * @param {number} params.pageNum - 
	 * @param {number} params.pageSize - 
	 * @param {string} params.userId - ID
	 * @param {number} params.settlementCycle - (0: 1: 2:)
	 */
	getEligibilityPage({
		pageNum = 1,
		pageSize = 10,
		userId,
		settlementCycle
	}) {
		const url =
			`/MonthlySettlementEligibility/page?pageNum=${pageNum}&pageSize=${pageSize}${userId ? `&userId=${userId}` : ''}${settlementCycle !== undefined ? `&settlementCycle=${settlementCycle}` : ''}`
		return request({
			url,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {Object} params
	 * @param {string} params.userId - ID
	 * @param {number} params.settlementCycle - (0: 1: 2:)
	 */
	getEligibilityList({
		userId,
		settlementCycle
	}) {
		const url =
			`/MonthlySettlementEligibility/list${userId ? `?userId=${userId}` : ''}${settlementCycle !== undefined ? `${userId ? '&' : '?'}settlementCycle=${settlementCycle}` : ''}`
		return request({
			url,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	getEligibilityById(id) {
		return request({
			url: `/MonthlySettlementEligibility?id=${id}`,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {Object} params
	 * @param {number} params.pageNum - 
	 * @param {number} params.pageSize - 
	 * @param {string} params.userId - ID
	 * @param {number} params.applyStatus - (0: 1: 2:)
	 */
	getApplyPage({
		pageNum = 1,
		pageSize = 10,
		userId,
		applyStatus
	}) {
		const url =
			`/MonthlySettlementApply/page?pageNum=${pageNum}&pageSize=${pageSize}${userId ? `&userId=${userId}` : ''}${applyStatus !== undefined ? `&applyStatus=${applyStatus}` : ''}`
		return request({
			url,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {Object} params
	 * @param {string} params.userId - ID
	 * @param {number} params.applyStatus - (0: 1: 2:)
	 */
	getApplyList({
		userId,
		applyStatus
	}) {
		const url =
			`/MonthlySettlementApply/list${userId ? `?userId=${userId}` : ''}${applyStatus !== undefined ? `${userId ? '&' : '?'}applyStatus=${applyStatus}` : ''}`
		return request({
			url,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	getApplyById(id) {
		return request({
			url: `/MonthlySettlementApply?id=${id}`,
			method: 'get'
		})
	},

	/**
	 * 
	 * @param {Object} apply - 
	 * @param {string} apply.userId - ID
	 * @param {number} apply.accountType - (0: 1:)
	 * @param {string} apply.name - 
	 * @param {string} apply.phone - 
	 * @param {string} apply.address - 
	 * @param {string} apply.email - 
	 * @param {string} apply.startTime - 
	 * @param {string} apply.endTime - 
	 * @param {number} apply.minimumAmount - 
	 * @param {string} apply.remark - 
	 * @param {number} apply.settlementCycle - (0: 1: 2:)
	 * @param {number} apply.invoiceRequirement - (0: 1: 2:)
	 * @param {string} apply.idCardPortraitPhoto - 
	 * @param {string} apply.idCardEmblemPhoto - 
	 * @param {string} apply.businessLicense - ()
	 */
	addApply(apply) {
		return request({
			url: '/MonthlySettlementApply',
			method: 'post',
			data: apply
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	cancelApply(id) {
		return request({
			url: `/MonthlySettlementApply/cancel?id=${id}`,
			method: 'put'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	deleteApply(id) {
		return request({
			url: `/MonthlySettlementApply?id=${id}`,
			method: 'delete'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	logoffEligibility(id) {
		return request({
			url: `/MonthlySettlementEligibility/logoff?id=${id}`,
			method: 'put'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 * @returns {Promise<number>} 0- 1-
	 */
	checkEffective(id, code) {
		return request({
			url: `/MonthlySettlementEligibility/isEffective?id=${id}&code=${code}`,
			method: 'get'
		})
	},
	getUser(id) {
		return request({
			url: `/MonthlySettlementEligibility/getUser?id=${id}`,
			method: 'GET'
		})
	},
	// api/monthly.js
	updatePassword(id, oldPassword, newPassword, userId) {
		return request({
			url: `/MonthlySettlementEligibility/updatePassword?id=${id}&oldPassword=${oldPassword}&newPassword=${newPassword}&userId=${userId}`,
			method: 'PUT'
		})
	},

	/**
	 * 
	 * @param {string} id - ID
	 */
	getEligibilityUsers(id) {
		return request({
			url: `/MonthlySettlementEligibility/getUsers?id=${id}`,
			method: 'GET'
		})
	},
	
	batchesAdd(data) {
	  return request({
	    url: '/MonthlyStatement/batchesAdd',
	    method: 'post',
	    data
	  })
	},
}

export default monthlyApi