import request from '@/utils/request'

const invoiceApi = {
  /**
   * 
   * @param {number} pageNum - 
   * @param {number} pageSize - 
   * @param {number} type - (0: 1:)
   * @param {string} userId - ID
   */
  getInvoicePage({ pageNum = 1, pageSize = 10, type, userId }) {
    return request({
      url: `/InvoiceTitle/page?pageNum=${pageNum}&pageSize=${pageSize}&type=${type}&userId=${userId}`,
      method: 'get'
    })
  },

  /**
   * 
   * @param {number} type - (0: 1:)
   * @param {string} userId - ID
   */
  getInvoiceList({ type, userId }) {
    return request({
      url: `/InvoiceTitle/list?type=${type}&userId=${userId}`,
      method: 'get'
    })
  },

  /**
   * 
   * @param {string} id - ID
   * @param {number} type - (0: 1:)
   */
  getDefaultInvoice({ id, type }) {
    return request({
      url: `/InvoiceTitle/getDefault?id=${id}&type=${type}`,
      method: 'get'
    })
  },

  /**
   * 
   * @param {string} id - ID
   */
  getInvoiceById(id) {
    return request({
      url: `/InvoiceTitle?id=${id}`,
      method: 'get'
    })
  },

  /**
   * 
   * @param {Object} invoice - 
   */
  addInvoice(invoice) {
    return request({
      url: '/InvoiceTitle',
      method: 'post',
      data: invoice
    })
  },

  /**
   * 
   * @param {Object} invoice - 
   */
  updateInvoice(invoice) {
    return request({
      url: '/InvoiceTitle',
      method: 'put',
      data: invoice
    })
  },

  /**
   * 
   * @param {string} id - ID
   */
  deleteInvoice(id) {
    return request({
      url: `/InvoiceTitle?id=${id}`,
      method: 'delete'
    })
  },

  /**
   * 
   * @param {Object} params - 
   * @param {string} params.userId - ID
   * @param {string} params.orderId - ID
   */
  getOrderInvoiceRequest({ userId, orderId }) {
    return request({
      url: `/OrderInvoiceRequest/list?userId=${userId}&orderId=${orderId}`,
      method: 'get'
    })
  },

  /**
   * 
   * @param {Object} data - 
   */
  addOrderInvoiceRequest(data) {
    return request({
      url: '/OrderInvoiceRequest',
      method: 'post',
      data
    })
  }
}

export default invoiceApi 