import request from '@/utils/request'

const postalApi = {
  /**
   * 
   */
  getProvinceCity() {
    return request({
      url: '/Postal/getProvinceCity',
      method: 'get'
    })
  },

  /**
   * 
   * @param {Object} params 
   * @param {number} params.pid - ID
   * @param {number} params.cid - ID
   * @param {number} [params.did] - ID
   * @param {string} [params.q] - 
   * @param {number} [params.pageNum=1] - 
   * @param {number} [params.pageSize=20] - 
   */
  getPostal(params) {
    const { pid, cid, did = '', q = '', pageNum = 1, pageSize = 20 } = params
    return request({
      url: `/Postal/getPostal?pid=${pid}&cid=${cid}&did=${did}&q=${q}&pageNum=${pageNum}&pageSize=${pageSize}`,
      method: 'get'
    })
  }
}

export default postalApi 